@import '../../../styles/variables.scss';

.text-field.text-field_number {
    box-sizing: border-box;
    width: 100%;
    max-height: 400px;
    height: 100%;
    padding-top: 12px;
    padding-left: 19px;
    font-family: $mainFont;
    font-size: 13px;
    line-height: 14px;
    color: $text1;
    resize: none;
}

input.text-field_number::placeholder {
    transform: translateY(-74px) translateY(50%);
    letter-spacing: 0.5px;
}